import React from "react"
import { Helmet } from "react-helmet"

export default function Seo({ title, description }) {
  return (
    <Helmet>
      <title>{title ? `${title} | Psychology Site` : "Psychology Site"}</title>
      <meta
        name="description"
        content={description || "Психологические тесты и консультации"}
      />
    </Helmet>
  )
}
